.movie .inner {
  @media #{$min961} {
    padding-right: 60px;
    padding-left: 60px;
  }
}

.movie .ttl-page-lower {
  background-image: url(/movie/img/bg_movie_main.jpg);
}

.movie-player {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
  #player01 {
    position: absolute;
    top: 0;
    right: 0;
    width: 100% !important;
    height: 100% !important;
  }
}

.movie-nav {
  &__list {
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}flex-wrap: wrap;
    }
    margin-top: 30px;
    &-item {
      &:nth-child(n+2) {
        margin-left: 5px;
      }
      .movie-thumb__ttl {
        display: block;
        margin-top: 3%;
        color: $main_blue01;
        font-size: 1.4rem;
        line-height: 1.3;
        text-align: center;
      }
      &.on {
        img {
          border: 5px $main_blue02 solid;
          box-sizing: border-box;
        }
      }
      a {
        img {
          border: 5px #fff solid;
          &:hover {
            border: 5px $main_blue02 solid;
          }
        }
      }
    }
    @media #{$min768} {
      @each $prefix in $prefixList {
        #{$prefix}justify-content: center;
      }
    }
    @media #{$max480} {
      &-item {
        width: 48%;
        &:nth-child(odd) {
          margin-left: 0;
        }
        &:nth-child(n+3) {
          margin-top: 3%;
        }
      }
    }
  }
}

.movie {
  .link-special {
    margin-top: 9.8%;
    text-align: center;
    &__ttl {
      @include font-heavy;
      font-size: 2.4rem;
      color: $main_blue01;
      @media #{$max480} {
        font-size: 1.5rem;
      }
    }
    &__photo {
      margin-top: 15px;
    }
  }
}
