.btn {
  &-top {
    &-pdf {
      position: relative;
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 1047px;
      margin-top: -2%;
      margin-left: auto;
      margin-right: auto;
      padding: 25px 0 25px;
      background: #FFF;
      border: 2px solid #dcdcdc;
      color: $normal_font;
      font-size: 2.2rem;
      font-style: normal;
      @media #{$max767} {
        font-size: 1.4rem;
      }
      @media #{$max480} {
        font-size: 1.2rem;
      }
      &__inner {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      align-items: center;
      justify-content: center;
      }
      &__ico {
        flex-basis: 50px;
        max-width: 50px;
        margin-right: 20px;
        vertical-align: middle;
        @media #{$max767} {
          flex-basis: 34px;
          max-width: 34px;
          margin-right: 10px;
        }
      }
      &__txt {
        // vertical-align: middle;
        color: #dc1000;
        text-align: left;
        display: block;
        @media #{$max767} {
          vertical-align: bottom;
        }
      }
      &__alert {
        margin-left: 13px;
        color: #dc1000;
        font-size: 1.4rem;
        vertical-align: middle;
        @media #{$max767} {
          display: block;
          margin-top: 10px;
          margin-left: 0;
          font-size: 1.2rem;
        }
      }
      &:after {
        position: absolute;
        top: 50%;
        right: 18px;
        width: 11px;
        height: 11px;
        border-top: 1px solid #404061;
        border-right: 1px solid #404061;
        transform: rotate(45deg) translateY(-50%);
        content: '';
      }
      &:hover {
        opacity: .75;
      }
    }
  }
}

