.symptom {
  @media #{$min961} {
    .container {
      background: url(/symptom/img/bg_symptom_main_02.jpg) no-repeat center top;
    }
  }
}

.symptom .ttl-page-lower {
  background-image: url(/symptom/img/bg_symptom_main.jpg);
  color: $main_blue01;
  .ttl-cmn-01__en {
    &:before,
    &:after {
      background: $main_blue01;
    }
  }
  @media #{$max480} {
    padding: 0;
    .ttl-page-inner {
      padding: 11.2% 0 15.4%;
      //      background: rgba(#fff, .5);
    }
  }
}

.container__inner-symptom {
  max-width: 1020px;
  margin: 0 auto 5%;
  color: $main_blue01;
  a {
    color: $main_blue01;
  }
  @media #{$max960} {
    width: 89.58%;
  }
  @media #{$max480} {
    width: 100%;
  }
}

//common
.symptom-cotents__area {
  background: #fff;
  border: 5px #eee solid;
  .btn-area {
    padding: 2.4% 0;
    background: #eee;
    &__inner {
      display: block;
      position: relative;
      font-size: 3rem;
      margin: 0 auto;
      padding: 2.45% 0;
      text-align: center;
      width: 37.2%;
      background: #fff;
      border: 1px $main_blue01 solid;
      border-radius: 10px;
      &:before {
        content: "";
        position: absolute;
        background: url(/symptom/img/ico_btn_arrow.gif) no-repeat 0 0;
        left: 7.1%;
        top: 50%;
        transform: translateY(-50%);
        width: 30px;
        height: 30px;
        background-size: contain;
        transition: .5s;
      }
      &:hover {
        &:before {
          left: 5.1%;
        }
      }
    }
  }
  @media #{$max960} {
    .btn-area {
      &__inner {
        font-size: 2rem;
        &:before {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
  @media #{$max480} {
    .btn-area {
      padding: 5% 0;
      &__inner {
        width: 70%;
        padding: 4% 0;
      }
    }
  }
}

.symptom {
  .note {
    margin-top: 5px;
    text-align: right;
    font-size: 1.4rem;
    @media #{$max480} {
      font-size: 1rem;
    }
  }
}

//.symptom-research
.symptom-research {
  position: relative;
  padding-top: 14%;
  &.q-1 {
    &:before {
      content: "";
      background: url(/symptom/img/ico_num_01.gif) no-repeat 0 0;
      position: absolute;
      top: -70px;
      left: 50%;
      transform: translateX(-50%);
      width: 130px;
      height: 130px;
      @media #{$max768} {
        top: -56px;
        left: 50%;
        transform: translateX(-50%);
        width: 100px;
        height: 100px;
        background-size: contain;
        z-index: 2;
      }
      @media #{$max480} {
        top: -36px;
        width: 70px;
        height: 70px;
      }
    }
  }
  &.q-2 {
    &:before {
      content: "";
      background: url(/symptom/img/ico_num_02.gif) no-repeat 0 0;
      position: absolute;
      top: -70px;
      left: 50%;
      transform: translateX(-50%);
      width: 130px;
      height: 130px;
      @media #{$max768} {
        top: -56px;
        left: 50%;
        transform: translateX(-50%);
        width: 100px;
        height: 100px;
        background-size: contain;
        z-index: 2;
        @media #{$max480} {
          top: -36px;
          width: 70px;
          height: 70px;
        }
      }
    }
  }
  .q-ttl {
    font-size: 4rem;
    text-align: center;
    font-weight: bold;
  }
  .ico-num {
    position: absolute;
    top: -70px;
    left: 50%;
    transform: translateX(-50%);
  }
  //.symptom-research list-col
  .list-col {
    margin-top: 5.8%;
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}flex-wrap: wrap;
      #{$prefix}justify-content: center;
    }
    &__col {
      color: #fff;
      background: $main_blue01;
      border-radius: 10px;
      input {
        position: absolute;
        visibility: hidden;
        opacity: 0;
        width: 0;
        height: 0;
      }
      label {
        display: block;
        cursor: pointer;
        padding: 30px 0;
        line-height: 1.3;
      }
    }
    &__col02 {
      //      flex-basis: 37.2%;
      flex-basis: 33.2%;
      &:nth-child(even) {
        margin-left: 2.9%;
      }
      @media #{$max480} {
        flex-basis: 45%;
      }
    }
    &__col03 {
      flex-basis: 29.4%;
      &:nth-child(n+2) {
        margin-left: 2.45%;
      }
      @media #{$max480} {
        flex-basis: 45%;
        &:nth-child(3) {
          margin-top: 2.45%;
        }
      }
    }
  }
  
  @media #{$max480} {
    .q-ttl {
      font-size: 2rem;
    }
    .list-col {
      &__col {
        label {
          padding: 20px 0;
        }
      }
    }
  }
}

.q-1 {
  .symptom-research__inner {
    padding-bottom: 9.8%;
  }
  .list-col {
    &__col02 {
      //      font-size: 4.2rem;
      font-size: 3rem;
      text-align: center;
      font-weight: bold;
      &:nth-child(n+3) {
        margin-top: 30px;
      }
    }
  }
  @media #{$max960} {
    .list-col {
      &__col02 {
        font-size: 3rem;
        &:nth-child(n+3) {
          margin-top: 2.9%;
        }
      }
      &__col {
       font-size: 2.4rem; 
      }
    }
  }
  @media #{$max480} {
    .list-col {
      &__col02 {
        font-size: 1.5rem;
      }
    }
  }
}

.q-2 {
  .symptom-research__inner {
    padding-bottom: 7.8%;
    .list-col {
      &__col {
        font-size: 2.2rem;
        @each $prefix in $prefixList {
          #{$prefix}display: flex;
          #{$prefix}justify-content: center;
          #{$prefix}flex-wrap: wrap;
        }
        span {
          display: block;
          text-indent: -1em;
          padding-left: 1em;
        }
        span.bold {
          font-size: 2.4rem;
        }
      }
    }
    @media #{$max960} {
      .list-col {
        &__col {
          font-size: 1.5rem;
          span.bold {
            font-size: 1.4rem;
          }
        }
      }
    }
  }
}

//.symptom-result
.symptom-result {
  .answer-ttl {
    padding: 3.3% 0;
    text-align: center;
    color: #fff;
    font-size: 3.4rem;
    @include font-heavy;
    font-style: normal;
    background: -webkit-gradient(radial, 49% 50%, 0, 49% 50%, 122, color-stop(0.88, #2c2c51), color-stop(0.00, #213271));
    background: -webkit-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
    background: -moz-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
    background: -o-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
    background: -ms-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
    background: radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  }
  &__inner {
    padding: 6.8% 0 21.5%;
    &.result-type-03 {
      padding-bottom: 8.3%;
    }
  }
  .answer-ttl-sub {
    font-size: 3rem;
    color: $normal_font;
    text-align: center;
    font-weight: bold;
    .txt-blue {
      color: $main_blue02;
      font-size: 4rem;
    }
  }
  .answer-ttl-explain {
    margin-top: 4.6%;
    font-size: 2.4rem;
    line-height: 1.3;
    @media #{$min961} {
      text-align: center;
    }
  }
  .result-type-01 {
    background: url(/symptom/img/bg_result_01.jpg) no-repeat right bottom;
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}justify-content: center;
      #{$prefix}flex-wrap: wrap;
    }
  }
  .result-type-02 {
    background: url(/symptom/img/bg_result_02.jpg) no-repeat right bottom;
    @extend .result-type-01;
  }
  @media #{$max960} {
    &__inner {
      padding: 6.8% 3% 30%;
    }
    .answer-ttl-sub {
      line-height: 1.3;
    }
    .result-type-01,
    .result-type-02 {
      background-size: 50%;
    }
  }
  @media #{$max480} {
    .answer-ttl {
      font-size: 2.5rem;
      &-sub {
        font-size: 1.7rem;
        .txt-blue {
          font-size: 2rem;
        }
      }
      &-explain {
        font-size: 1.5rem;
      }
    }
  }
}

.suggest {
  margin-top: 60px;
  &-ttl {
    margin-bottom: 10px;
    padding: 2.2% 0;
    background: $main_blue02;
    color: #fff;
    text-align: center;
    font-size: 1.8rem;
  }
  &-mod {
    flex-basis: 44%;
  }
  &-list {
    &__items {
      &:nth-child(n+2) {
        margin-top: 5.2%;
      }
    }
  }
  @media #{$min961} {
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}justify-content: center;
    }
    &-list {
      &__items {
        border: 5px solid #FFF;
        transition: .2s;
        a {
          display: block;
          padding: 3%;
        }
        &:hover {
          border-color: #EEE;
        }
      }
    }
    .suggest-2 {
      margin-left: 4%;
    }
  }
  @media #{$max960} {
    margin-top: 7%;
    &-ttl {
      margin-bottom: 10%;
    }
    &-list {
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: center;
        #{$prefix}flex-wrap: wrap;
      }
      &__items {
        &:nth-child(n+2) {
          margin-top: 15%;
        }
      }
    }
  }
  @media #{$max960} {
    .suggest-2 {
      margin-top: 15%;
    }
  }
}
