.error {
  text-align: center;
  .inner {
    margin: 5% auto;
  }
  &-ttl {
    font-size: 3.5rem;
    color: $main_blue01;
    @include font-heavy;
  }
  &-txt01 {
    margin-top: 30px;
    font-size: 2rem;
    font-weight: bold;
  }
  &-txt02 {
    margin-top: 40px;
    line-height: 1.4;
    font-size: 1.5rem;
  }
  @media #{$max480} {
    .inner {
      margin: 15% auto;
      .error-ttl {
        font-size: 3rem;
      }
      .error-txt01 {
        margin-top: 30px;
        font-size: 1.4rem;
      }
    }
  }
}
