.sitemap .ttl-page-lower {
  background-image: url(/sitemap/img/bg_product_main.jpg);
}

.sitemap-area {
  @extend .inner;
  @include font-heavy;
  a {
    color: $main_blue01;
  }
  @media #{$max960} {
    width: 100%;
  }
}

.sitemap {
  .border-bottom--blue {
    border-bottom: 1px $main_blue01 solid;
  }
  &-list {
    font-size: 2.4rem;
    @media #{$max480} {
      font-size: 2rem;
    }
    .sitemap-link {}
    &__inner {
      padding: 2.6% 0 2.6% 3.12%;
      @media #{$min480} {
        @each $prefix in $prefixList {
          #{$prefix}display: flex;
          #{$prefix}flex-wrap: wrap;
        }
      }
    }
    &__items {
      &-ttl {
        font-size: 2rem;
        font-weight: bold;
      }
      &-child {
        margin-top: 20px;
        font-size: 1.4rem;
        font-weight: normal;
        @include font-meiryo;
        li+li {
          margin-top: 10px;
        }
      }
      &:nth-child(n+2) {
        @media #{$min480} {
          margin-left: 4%;
        }
      }
      @media #{$max480} {
        &:nth-child(n+2) {
          margin-top: 5%;
        }
        &-ttl {
          font-size: 1.5rem;
        }
      }
    }
  }
  &-link {
    display: block;
    position: relative;
    padding: 1.5%;
    border-top: 1px $main_blue01 solid;
    line-height: 1.6;
    &:after {
      content: "";
      position: absolute;
      border-style: solid;
      border-width: 7.5px 0 7.5px 12px;
      border-color: transparent transparent transparent $main_blue01;
      right: 3%;
      top: 39%;
      transition: .5s;
      transition-property: all;
    }
    @media #{$min961} {
      &:hover {
        &:after {
          right: 1.5%;
        }
      }
    }
    @media #{$max960} {
      padding: 3.3% 5.2%;
      line-height: 1.2;
      &:after {
        right: 5.2%;
      }
    }
  }
  &-link--page-move {
    @extend .sitemap-link;
    &:after {
      background: url(/common/img/ico_page_change.gif) no-repeat 0 0;
      top: 50%;
      right: 3%;
      transform: translateY(-50%);
      width: 14px;
      height: 12px;
      background-size: cover;
      border: none;
      @media #{$max960} {
        right: 5.2%;
      }
    }
  }
}
