@media #{$max960} {
  .which-contents {
    .hide {
      display: none;
    }
  }
}

.distinction {
  .ttl-page-lower {
    background-image: url(/distinction/img/bg_distinction_main_pc.jpg);
  }
  .inner {
    width: 100%;
  }
  @media #{$min961} {
    padding-bottom: 5.2%;
  }
  @media #{$max960} {
    .ttl-page-lower {
      background-image: url(/distinction/img/bg_distinction_main_sp.jpg);
      @media #{$max480} {
        .ttl-cmn-01__main {
          line-height: 1.3;
        }
      }
    }
  }
}

.contents-ttl {
  text-align: center;
  font-weight: bold;
  &__inner {
    position: relative;
    display: inline-block;
    background: #fff;
    font-size: 3.6rem;
    color: $main_blue01;
  }
  @media #{$min1021} {
    &__inner {
      &:before,
      &:after {
        content: "";
        position: absolute;
        background: $main_blue01;
        height: 2px;
        width: 200px;
        top: 50%;
        transform: translateY(-50%)
      }
      &:before {
        right: 100%;
        margin-right: 4.5%;
      }
      &:after {
        left: 100%;
        margin-left: 4.5%;
      }
    }
  }
  @media #{$max960} {
    position: relative;
    &__inner {
      padding: 0 2.6%;
    }
    &:before {
      display: block;
      content: "";
      position: absolute;
      background: $main_blue01;
      height: 3px;
      width: 100%;
      bottom: 50%;
      transform: translateY(-50%);
    }
    &-sub {
      margin-top: 6.56%;
      text-align: center;
      font-size: 3.5rem;
      font-weight: bold;
      &__inner {
        padding-right: 50px;
        position: relative;
        display: inline-block;
        &:after {
          content: "";
          position: absolute;
          right: 0;
          bottom: 0;
          background: url(/distinction/img/ico_hand_01.gif) no-repeat 0 0;
          width: 41px;
          height: 47px;
          background-size: contain;
        }
        .ttl-sub--size {
          font-size: 4rem;
        }
      }
    }
  }
  @media #{$max480} {
    &__inner {
      font-size: 1.8rem;
    }
    &-sub {
      font-size: 1.5rem;
      &__inner {
        padding-right: 30px;
        &:after {
          bottom: 0;
          background: url(/distinction/img/ico_hand_01.gif) no-repeat right bottom;
          background-size: contain;
          width: 21px;
          height: 27px;
        }
        .ttl-sub--size {
          font-size: 2rem;
        }
      }
    }
  }
}

.which {
  margin-top: 5.3%;
  .which-tab {
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}justify-content: space-between;
    }
    &__anchor {
      text-align: center;
      .anchor-ico {
        margin-top: 20px;
        @media #{$max960} {
          margin-bottom: 5.6%;
        }
      }
      &.select {
        .anchor-anime {
          transform: scale(1.1);
          transform-origin: center;
        }
      }
      @media #{$min961} {
        flex-basis: 40%;
        margin: 0 auto;
      }
    }
  }
  &-contents {
    margin-top: 2.9%;
  }
  @media #{$min961} {
    &-contents {
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: space-between;
      }
      .which-box {
        flex-basis: 49%;
      }
    }
  }
  @media #{$max960} {
    .which-tab {
      &__anchor {
        flex-basis: 49%;
        .which-btn {
          display: block;
          width: 90%;
          &.current {
            width: 100%;
          }
        }
      }
    }
  }
  @media #{$max480} {
    &-tab {
      .anchor-ico {
        img {
          width: 50%;
        }
      }
    }
  }
}

.which {
  .bg-green {
    background: #257066;
  }
  .bg-blue {
    background: #0c528a;
  }
  .c-green {
    color: #257066;
  }
  .c-blue {
    color: #0c528a;
  }
  &-header {
    padding: 5.4% 0;
    font-weight: bold;
    font-size: 2.8rem;
    text-align: center;
    color: #fff;
    @include font-heavy;
    font-style: normal;
    &--size {
      @media #{$min961} {
        font-size: 4rem;
      }
      font-size: 5rem;
      display: inline-block;
      padding-left: 15px;
    }
  }
  &-body {
    &__inner {
      background: #fff;
    }
  }
  .box-why {
    margin-top: 40px;
    text-align: center;
    &__ttl {
      position: relative;
      display: inline-block;
      text-align: left;
      font-size: 3rem;
      padding-left: 80px;
      font-weight: bold;
      line-height: 1.2;
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        background: url(/distinction/img/ico_hatena_green.gif) no-repeat left center;
        background-size: contain;
        width: 15.3%;
        height: 100%;
      }
      &.c-blue {
        &:before {
          background: url(/distinction/img/ico_hatena_blue.gif) no-repeat left center;
          background-size: contain;
          width: 15.3%;
          height: 100%;
        }
      }
      &--size {
        font-size: 3.4rem;
      }
    }
    &__txt {
      display: inline-block;
      text-align: left;
      margin-top: 30px;
      font-size: 2rem;
      color: $normal_font;
      background: url(/distinction/img/bg_border_dotted_01.gif) repeat top left;
      line-height: 41px;
      font-weight: bold;
      @media #{$max480} {
        font-size: 1.5rem;
        background: url(/distinction/img/bg_border_dotted_01_sp.gif) repeat top left;
        line-height: 30px;
      }
    }
  }
  .box-effect {
    margin-top: 40px;
    &__txt {
      margin-top: 40px;
    }
  }
  .box-link {
    @include font-black;
    text-align: center;
    &__ttl {
      padding: 17px 0 15px;
      color: #fff;
      font-size: 3rem;
      line-height: 1.2;
    }
    &__photo {
      padding: 11px 0 14px;
      background: #fff;
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: center;
        #{$prefix}align-items: center;
      }
    }
    .link-ttl--size {
      font-size: 4rem;
    }
  }
  @media #{$min961_max1076} {
    &-header {
      font-size: 2rem;
      &--size {
        font-size: 4rem;
      }
    }
    .box-why {
      &__ttl {
        font-size: 2rem;
        &--size {
          font-size: 2.5rem;
        }
      }
      &__txt {
        height: 328px;
      }
    }
    .box-link {
      &--size {
        font-size: 4rem;
      }
    }
  }
  @media #{$min961} {
    &-box {
      padding: 0 2px;
    }
    &-body__inner {
      padding: 5% 4.2% 0;
      .which-goods {
        width: 80%;
        margin: 0 auto;
      }
    }
    .box-link {
      &__photo {
        margin: 0 8px 8px;
      }
    }
    .box-effect__txt {
      margin: 40px auto 0;
      width: 80%;
    }
  }
  @media #{$max960} {
    &-contents {
      margin-top: 7.29%;
    }
    &-body {
      padding: 0 2.08% 2.08%;
      &__inner {
        padding: 4.6% 3.4% 0;
      }
    }
    .box-why {
      &__ttl {
        font-size: 2.5rem;
        padding-left: 16%;
        @media #{$min640_max960} {
          font-size: 4rem;
        }
      }
    }
    .box-link {
      &__ttl {
        padding: 3.26% 0;
        font-size: 3.4rem;
      }
      .link-ttl--size {
        font-size: 4.5rem;
      }
    }
  }
  @media #{$max480} {
    &-header {
      font-size: 1.8rem;
      &--size {
        font-size: 3rem;
      }
    }
    .box-why {
      &__ttl {
        font-size: 2rem;
        &--size {
          font-size: 2.5rem;
        }
      }
    }
    .box-link {
      &__ttl {
        font-size: 2.4rem;
      }
      .link-ttl--size {
        font-size: 2.7rem;
      }
    }
  }
}

@media #{$max960} {
  .which-box--kyotan {
    .box-link {
      &__photo {
        padding: 5.8% 0 3.25%;
      }
      .link__photo-item {
        width: 49.3%;
        margin: 0 auto;
        display: block;
      }
    }
  }
}

@media #{$max960} {
  .which-box--seki {
    .box-link {
      &__photo {
        padding: 1.27% 3.4% 3.02%;
      }
      .link__photo-item {
        width: 51.6%;
        &:nth-child(2) {
          width: 48.3%;
        }
      }
    }
  }
}
