.list {
  &-bread {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    font-size: 1.2rem;
    &__item {
      color: #FFF;
      &+& {
        &:before {
          margin: 0 10px;
          content: '＞';
        }
      }
    }
    &__ico {
      margin-right: 5px;
      vertical-align: top;
    }
    &__txt {
      color: #FFF;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &-product {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media #{$max960} {
      display: block;
    }
    &__item {
      flex-basis: 49%;
      border: 5px solid #FFF;
      transition: .2s;
      @media #{$max960} {
        border-width: 3px;
      }
      &:hover {
        @media #{$min961} {
          border-color: #EEE;
        }
      }
      &:nth-child(n + 3) {
        margin-top: 20px;
        @media #{$max960} {
          margin-top: 17.64%;
        }
      }
      &+& {
        @media #{$max960} {
          margin-top: 17.64%;
        }
      }
    }
    &__inner {
      display: block;
      padding: 35px 25px 40px;
      @extend .clearfix;
      @media #{$max960} {
        position: relative;
        padding: 0;
      }
      &--tate {
        @extend .list-product__inner;
        padding-right: 10px;
        @media #{$max960} {
          padding-right: 0;
        }
      }
      .list-product__btn {
        margin-top: 25px;
        @media #{$max960} {
          float: none;
          margin-top: 10px;
        }
      }
    }
    &__detail {
      margin-top: 15px;
      color: $normal_font;
      line-height: 1.4;
      @media #{$max960} {
        margin-top: 10px;
      }
      &--yoko {
        @extend .list-product__detail;
        float: left;
        width: 57%;
        padding-right: 20px;
        @media #{$max960} {
          padding-right: 10px;
        }
        &.epia {
          @media #{$max960} {
            margin-top: 50px;
          }
        }
      }
      &--tate {
        float: left;
        width: 66%;
        @media #{$max960} {
          width: 60%;
        }
      }
    }
    &__txt {
      min-height: 100px;
      @media #{$max960} {
        min-height: auto;
        font-size: 1.2rem;
      }
    }
    &__photo {
      float: right;
      &--yoko {
        @extend .list-product__photo;
        width: 43%;
        margin-top: -8px;
        text-align: center;
        @media #{$max960} {
          position: absolute;
          right: 0;
          bottom: 0;
          float: none;
          margin-top: 0;
        }
      }
      &--tate {
        @extend .list-product__photo;
        width: 34%;
        margin-top: 32px;
        text-align: center;
        @media #{$max960} {
          position: absolute;
          right: 0;
          bottom: 0;
          float: none;
          width: 38%;
          margin-top: 0;
          img {
            max-height: 150px;
          }
        }
      }
    }
  }
  &-category {
    position: relative;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    -o-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: 10px;
    z-index: 10;
    &.list-category-line-2 {
      margin-top: 6px;
      .list-category__item {
        margin-top: 0;
      }
      @media #{$max960} {
        margin-top: 5px;
      }
    }
    @media #{$max960} {
      margin-top: 0;
    }
    &__item {
      margin: 5px 6px 0 0;
      padding: 3px 7px;
      border-radius: 3px;
      color: #FFF;
      font-weight: bold;
      font-style: normal;
      @media #{$max960} {
        padding: 2px 7px;
        font-size: 1.3rem;
      }
      &.green {
        background: #0b5146;
      }
      &.orange {
        background: #dd9311;
      }
      &.blue {
        background: #2b2f52;
      }
      &.pink {
        background: #d55f7b;
      }
      &.light-blue {
        background: #00a0e9;
      }
      &.purple {
        background: #6a005f;
      }
      &.light-green {
        background: #00958c;
      }
      &.red-orange {
        background: #ec6941;
      }
      &.brown {
        background: #753a00;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &--style-none {
    list-style-type: none;
  }
}

.font {
  &-sm {
    font-size: 1.2rem;
  }
  &-default {
    font-size: 1.4rem;
  }
  &-mid {
    font-size: 1.6rem;
  }
  &-big {
    font-size: 2.4rem;
  }
  &-red {
    color: #dc1000;
  }
  &-color-normal {
    color: $normal_font !important;
  }
}

.float {
  &-left {
    float: left;
  }
  &-right {
    float: right;
  }
  &-none {
    float: none !important;
  }
}

.ttl {
  &-product {
    color: #00275f;
    font-size: 2.4rem;
    font-weight: bold;
    @include font-black;
    &--font-normal {
      @extend .ttl-product;
      @include font-meiryo;
      @extend .font-color-normal;
    }
    @media #{$max960} {
      font-size: 2.0rem;
    }
    @media #{$max960} {
      position: relative;
      font-size: 1.6rem;
      z-index: 10;
    }
    &--medium {
      @extend .ttl-product;
      font-size: 3.4rem;
      @media #{$min640_max960} {
        font-size: 2.6rem;
      }
      @media #{$max640} {
        font-size: 1.6rem;
      }
    }
    &--topbien01 {
      @extend .ttl-product;
      font-size: 3.8rem;
      @media #{$min640_max960} {
        font-size: 2.6rem;
      }
      @media #{$max640} {
        font-size: 1.6rem;
      }
    }
    &--big {
      @extend .ttl-product;
      font-size: 3.4rem;
      @media #{$max960} {
        font-size: 2.4rem;
      }
    }
    &__catch {
      display: block;
      margin-bottom: 10px;
      color: #dc1000;
      font-size: 1.8rem;
      font-weight: bold;
      @media #{$max960} {
        font-size: 1.6rem;
      }
      @media #{$max960} {
        font-size: 1.4rem;
      }
      &--big {
        @extend .ttl-product__catch;
        margin-bottom: 20px;
        font-size: 2.4rem;
        @media #{$max960} {
          margin-bottom: 10px;
          font-size: 1.8rem;
        }
      }
    }
    sub,
    sup {
      font-size: 1.4rem;
      @media #{$max960} {
        font-size: 1.2rem;
      }
    }
    &__strong {
      font-size: 5.0rem;
      display: block;
      @media #{$min961_max1076} {
        font-size: 4rem;
      }
      @media #{$max960} {
        font-size: 3.2rem;
      }
    }
  }
  &-cmn-01 {
    @include font-black;
    text-align: center;
    span {
      //恐らくspanくらいしか入れ子にしないので、あえてタグ名で指定してます
      //font-weight: bold;
    }
    &__main {
      display: block;
      margin-bottom: 20px;
      font-size: 4.8rem;
      @media #{$max960} {
        margin-bottom: 10px;
        font-size: 3.0rem;
      }
      @media #{$max480} {
        font-size: 2.3rem;
      }
    }
    &__en {
      position: relative;
      font-size: 2.4rem;
      @media #{$max960} {
        font-size: 1.6rem;
      }
      &:before,
      &:after {
        position: absolute;
        top: 50%;
        width: 120px;
        height: 3px;
        background: $normal_font;
        transform: translateY(-50%);
        content: '';
        @media #{$max960} {
          width: 80px;
          height: 2px;
        }
      }
      &:before {
        right: 100%;
        margin-right: 9px;
      }
      &:after {
        left: 100%;
        margin-left: 9px;
      }
    }
  }
  &-page-lower {
    padding: 110px 0 107px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    color: #FFF;
    @media #{$max960} {
      padding: 11.2% 0 10.4%;
    }
    .ttl-cmn-01__en {
      &:before,
      &:after {
        background: #FFF;
      }
    }
    &.product {
      background-image: url(/product/img/bg_product_main.jpg);
    }
  }
}

.intro {
  &-product {
    padding: 80px 0 75px 20px;
    @media #{$max960} {
      padding: 9.375% 0 10.4%;
    }
    &__inner {
      @extend .clearfix;
    }
    &__txt-area {
      @media #{$min961} {
        float: left;
        width: 50.4%;
        font-size: 1.6rem;
      }
      @media #{$max960} {
        font-size: 1.2rem;
      }
    }
    &__photo {
      @media #{$min961} {
        float: right;
        width: 46.3%;
      }
      @media #{$max960} {
        margin-top: 20px;
      }
    }
    &__comment {
      margin-top: 30px;
      line-height: 1.4;
      @media #{$min961} {
        float: left;
        width: 50.4%;
        font-size: 1.6rem;
      }
      @media #{$max960} {
        font-size: 1.4rem;
      }
    }
  }
}

.btn {
  &-pdf {
    position: relative;
    display: block;
    padding: 6px 10px 10px 15px;
    background: #FFF;
    border: 1px solid #dcdcdc;
    color: $normal_font;
    font-size: 1.6rem;
    @media #{$max960} {
      font-size: 1.4rem;
    }
    &__ico {
      margin-right: 13px;
      vertical-align: middle;
    }
    &__txt {
      vertical-align: middle;
      @media #{$max960} {
        vertical-align: bottom;
      }
    }
    &__alert {
      margin-left: 13px;
      color: #dc1000;
      font-size: 1.4rem;
      vertical-align: middle;
      @media #{$max960} {
        display: block;
        margin-top: 10px;
        margin-left: 0;
        font-size: 1.2rem;
      }
    }
    &:after {
      position: absolute;
      top: 50%;
      right: 18px;
      width: 11px;
      height: 11px;
      border-top: 1px solid #404061;
      border-right: 1px solid #404061;
      transform: rotate(45deg) translateY(-50%);
      content: '';
    }
  }
}

.table {
  &-product-01 {
    max-width: 1020px;
    margin: 0 auto;
    @media #{$max960} {
      margin-top: 0;
    }
    &__th,
    &__td {
      padding: 20px 25px;
      line-height: 1.5;
      vertical-align: top;
      @media #{$max960} {
        display: block;
        padding: 0 4.6% 5.2%;
      }
    }
    &__th {
      @include font-heavy;
      width: 160px;
      color: #2c2c51;
      font-size: 1.8rem;
      @media #{$max960} {
        width: 100%;
        padding-top: 5.2%;
        padding-bottom: 2.6%;
        font-size: 1.5rem;
      }
    }
    &__td {
      font-size: 1.6rem;
      @media #{$max960} {
        font-size: 1.3rem;
      }
    }
    &__tr {
      border-bottom: 1px solid #dcdcdc;
      &:last-child {
        border-bottom: 0;
        .table-product-01__th,
        .table-product-01__td {
          padding-bottom: 0;
        }
      }
    }
  }
  &-product-02 {
    width: 100%;
    margin-top: 10px;
    background: #FFF;
    font-size: 1.4rem;
    @media #{$max960} {
      font-size: 1.2rem;
    }
    &--fixed {
      @extend .table-product-02;
      table-layout: fixed;
    }
    &__th,
    &__td {
      padding: 15px;
      border: 1px solid #dcdcdc;
      font-weight: normal;
      @media #{$max960} {
        padding: 10px;
      }
      &.gray {
        background: #f2f2f2;
      }
      &.blue {
        background: #e5edf4;
      }
      &__conponent {
        @extend .table-product-02__th;
        width: 180px;
        font-size: 1.6rem;
        vertical-align: top;
        @media #{$max960} {
          width: 100px;
          font-size: 1.4rem;
        }
      }
    }
    &__head {
      text-align: center;
    }
  }
}

.product {
  &-section {
    padding: 20px 0 50px;
    border-bottom: 1px solid #dcdcdc;
    @media #{$max960} {
      padding: 2% 0 8.3%;
    }
  }
  &-bnr {
    padding: 50px 0 60px;
    text-align: center;
    @media #{$max960} {
      padding: 9.375% 0 10.4%;
    }
    &-list {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      justify-content: space-between;
      @media #{$max960} {
        flex-wrap: wrap;
      }
      &__item {
        flex-basis: 32.35%;
        @media #{$max960} {
          flex-basis: 48.83%;
        }
        &--wide {
          @extend .product-bnr-list__item;
          @media #{$max960} {
            flex-basis: 100%;
            margin-bottom: 2.34%;
          }
        }
      }
    }
  }
}
